<template>
  <div>
    <input type="text" v-model="input" placeholder="" />
    <el-button
      class="search"
      slot="append"
      icon="el-icon-search"
      @click="search(input)"
    ></el-button>
    <!--  -->
    <div class="addButton">
      <button @click="handleAdd"></button>
      <button @click="handleExcel">Excel</button>
    </div>
    <!--  -->
    <el-dialog
      class="addDialog"
      title=""
      :visible.sync="dialogVisible1"
      width="52%"
      :before-close="handleClose"
    >
      <!--  -->
      <el-form
        ref="form"
        :rules="rules"
        :model="form"
        :inline="true"
        label-width="80px"
      >
        <el-form-item label="" prop="name">
          <el-input placeholder="" v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="sex">
          <el-select v-model="form.sex" placeholder="">
            <el-option label="" :value="0"></el-option>
            <el-option label="" :value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="identity">
          <el-select v-model="form.identity" placeholder="">
            <el-option label="" :value="0"></el-option>
            <el-option label="" :value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="age">
          <el-input placeholder="" v-model="form.age"></el-input>
        </el-form-item>
        <el-form-item label="" prop="position">
          <el-select v-model="form.position" placeholder="">
            <el-option label="" :value="2"></el-option>
            <el-option label="" :value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="title">
          <el-select v-model="form.title" placeholder="">
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="major">
          <el-select v-model="form.major" placeholder="">
            <el-option label="" :value="0"></el-option>
            <el-option label="" :value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input placeholder="" v-model="form.email"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button @click="submit">  </el-button>
      </span>
    </el-dialog>
    <!-- excel -->
    <el-dialog
      class="addDiaglog"
      title="Excel"
      :visible.sync="dialogVisible2"
      width="40%"
    >
      <el-upload
        class="upload-demo"
        action="#"
        :before-upload="beforeUpload"
        accept=".xls, .xlsx"
        :before-remove="beforeRemove"
        :multiple="false"
        :limit="1"
        :on-exceed="handleExceed"
        :file-list="fileList"
      >
        <el-button size="small" type="primary"></el-button>
        <div slot="tip" class="el-upload__tip">
          xls/xlsx500kb
          <a @click="downloadFile"></a>
        </div>
      </el-upload>
    </el-dialog>
    <!--  -->
    <div class="common-table">
      <el-table
        height="90%"
        :data="pageTicket"
        style="width: 100%"
        stripe
        empty-text=""
      >
        <el-table-column prop="index" label="#" min-width="6.4%">
          <template slot-scope="scope">
            <span>{{ scope.$index + 1 }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="name" label="" min-width="14%">
        </el-table-column>
        <el-table-column prop="sex" label="" min-width="6.4%">
          <template slot-scope="scope">
            <span>{{ scope.row.sex == 1 ? "" : "" }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="age" label="" min-width="6.4%">
        </el-table-column>
        <el-table-column prop="position" label="" min-width="6.4%">
          <template slot-scope="scope">
            <span>{{ scope.row.position == 1 ? "" : "" }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="identity" label="" min-width="9.4%">
          <template slot-scope="scope">
            <span>{{ scope.row.identity == 1 ? "" : "" }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="title" label="" min-width="6.4%">
          <template slot-scope="scope">
            <span>{{ scope.row.title == 1 ? "" : "" }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="major" label="" min-width="17%">
          <template slot-scope="scope">
            <span>{{
              scope.row.major == 1 ? "" : ""
            }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="email" label="" min-width="17%">
        </el-table-column>
        <el-table-column label="" min-width="17%">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.row)"
              >
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.row)"
              >
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--  -->
    <div class="footer">
      <div class="block">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentpage"
          :page-size="pagesize"
          layout="total, prev, pager, next, jumper"
          :total="tableData.length"
        >
        </el-pagination>
      </div>
      <button class="reset" @click="reset"></button>
    </div>
  </div>
</template>

<script>
import {
  downLoad,
  getTeacherMessage,
  uploadExcel,
  deleteTeacher,
  editTeacher,
} from "../utils/api";
import { addTeacher } from "../utils/api";
export default {
  name: "TeacherManage",
  data() {
    return {
      input: "",
      //token
      dialogVisible1: false, //
      dialogVisible2: false, //excel
      fileList: [],
      form: {
        age: "",
        email: "",
        identity: "",
        major: "",
        name: "",
        position: "",
        sex: "",
        title: "",
      },
      modalType: 0, // 01
      rules: {
        name: [{ required: true, message: "" }],
        age: [{ required: true, message: "" }],
        sex: [{ required: true, message: "" }],
        email: [
          { required: true, message: "" },
          { validator: this.checkEmail, trigger: "blur" },
        ],
        title: [{ required: true, message: "" }],
        major: [{ required: true, message: "" }],
        position: [{ required: true, message: "" }],
        identity: [{ required: true, message: "" }],
      },
      tableData: [],
      currentpage: 1, //
      pagesize: 10, // 10
      pageTicket: [], //
    };
  },
  methods: {
    //email
    checkEmail(rule, value, cb) {
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/;
      if (regEmail.test(value)) {
        //
        return cb();
      }
      //
      cb(new Error(""));
    },
    //
    search(input) {
      this.pageTicket = this.tableData.filter((data) => {
        return data.name === input;
      });
    },
    handleExcel() {
      this.dialogVisible2 = true;
    },
    beforeUpload: function (file) {
      var fd = new window.FormData();
      fd.append("file", file);
      uploadExcel(fd).then((res) => {
        if (res.data.code == 0) {
          this.$message.success("");
        } else {
          this.$message.error("");
        }
      });
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        ` 1  ${files.length}  ${
          files.length + fileList.length
        } `
      );
    },
    beforeRemove(file, fileList) {
      return this.$confirm(` ${file.name}`);
    },
    //
    submit() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          if (this.modalType === 0) {
            addTeacher(this.form)
              .then((res) => {
                if (res.data.code == 0) {
                  this.$message.success("");
                  this.getList();
                  this.dialogVisible1 = false;
                  this.$refs.form.resetFields();
                } else {
                  this.$message.error("");
                }
              })
              .catch((err) => {
                console.log(err);
              });
          } else {
            console.log(this.form, "1");
            if (this.form.position === "") {
              this.form.position = 2;
            } else {
              this.form.position = 1;
            }
            if (this.form.title === "") {
              this.form.title = 1;
            } else {
              this.form.title = 2;
            }
            editTeacher({
              id: this.form.id,
              name: this.form.name,
              sex: this.form.sex,
              age: this.form.age,
              position: this.form.position,
              pTitle: this.form.title,
              major: this.form.major,
              email: this.form.email,
              identity: this.form.identity,
            })
              .then((res) => {
                if (res.data.code === 0) {
                  this.$message.success("");
                  this.getList();
                  this.dialogVisible1 = false;
                  this.$refs.form.resetFields();
                } else {
                  this.$message.error("");
                  console.log(res.data);
                  console.log(this.form);
                }
              })
              .catch((err) => {
                console.log(err);
              });
          }
        }
      });
    },
    //
    cancel() {
      this.handleClose();
    },
    //
    handleAdd() {
      this.modalType = 0;
      this.dialogVisible1 = true;
    },
    //
    downloadFile() {
      downLoad().then((res) => {
        console.log(res.data,'res.data');
        const blob = new Blob([res.data], {
          type: "application/vnd.ms-excel",
        });
        const link = document.createElement("a"); // a
        link.href = window.URL.createObjectURL(blob); // href
        link.download = ""; // dowload
        link.click(); // click()
      });
    },
    //
    handleClose() {
      this.dialogVisible1 = false;
      this.$refs.form.resetFields();
      // console.log(this.form);
    },
    //
    handleDelete(row) {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then(() => {
        deleteTeacher({ id: row.id })
          .then((res) => {
            if (res.data.code === 0) {
              this.$message.success("");
              this.tableData = this.tableData.filter((item) => {
                return item.id !== row.id;
              });
              this.getPageInfo();
            } else {
              this.$message.error("");
            }
          })
          .catch((err) => {
            console.log(err);
          });
      });
    },
    //
    handleEdit(row) {
      this.modalType = 1;
      this.dialogVisible1 = true;
      this.$nextTick(() => {
        this.form = JSON.parse(JSON.stringify(row));
      });
    },
    //
    getList() {
      getTeacherMessage()
        .then((res) => {
          if (res.data.code === 0) {
            this.tableData = res.data.data;
            this.getPageInfo();
          } else {
            this.$message.error("");
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getPageInfo() {
      //pageTicket
      this.pageTicket = [];
      // 
      for (
        let i = (this.currentpage - 1) * this.pagesize;
        i < this.tableData.length;
        i++
      ) {
        //pageTicket
        this.pageTicket.push(this.tableData[i]);
        //
        if (this.pageTicket.length === this.pagesize) break;
      }
    },
    handleSizeChange(size) {
      //
      this.pagesize = size;
      //
      this.getPageInfo();
    },
    handleCurrentChange(pageNumber) {
      //
      this.currentpage = pageNumber;
      //
      this.getPageInfo();
    },
    //
    reset() {
      this.getList();
      this.input = "";
    },
  },
  mounted() {
    this.getList();
    window.addEventListener(
      "popstate",
      function (e) {
        window.location.href = "/table#/table";
      },
      false
    );
  },
};
</script>

