import { request } from './baseURL' // 
import Cookie from 'js-cookie';
//
export function uploadExcel(data) {
    return request({
        url: '/api/student/excel/import',
        headers: {
            "Content-Type": "multipart/form-data",
            token: Cookie.get('token')
        },
        method: 'post',
        data: data
    })
}
//
export function addStudent(data) {
    return request({
        url: '/api/student/add',
        headers: {
            'Content-Type': 'application/json',
            token: Cookie.get('token')
        },
        method: 'post',
        data: data
    })
}
//
export function editStudent(data) {
    return request({
        url: '/api/student/update',
        headers: {
            'Content-Type': 'application/json',
            token: Cookie.get('token')
        },
        method: 'post',
        data: data
    })
}
//excel
export function downLoad() {
    return request({
        url: '/api/student/excel/export',
        responseType: "blob",
        headers: {
            "Content-Type": "application/json",
            token: Cookie.get("token"),
        },
        method: 'get',
    })
}
//
export function deleteStudent(data) {
    return request({
        url: '/api/student/delete',
        headers: {
            'Content-Type': 'application/json',
            token: Cookie.get('token')
        },
        method: 'post',
        data: data
    })
}
//
export function getStudentMessage() {
    return request({
        url: '/api/student/list',
        headers: {
            'Content-Type': 'application/json',
            token: Cookie.get('token')
        },
        method: 'get'
    })
}