import { request } from './baseURL' // 
import Cookie from 'js-cookie'
export function getPublishList() {
  return request({
    url: '/api/evaluate/list',
    headers: {
      'Content-Type': 'application/json',
      token: Cookie.get('token')
    },
    method: 'get'
  })
}
export function addPublish(data) {
  return request({
    url: '/api/evaluate/add',
    headers: {
      'Content-Type': 'application/json',
      token: Cookie.get('token')
    },
    method: 'post',
    data: data
  })
}
