//excel
export function downLoad(data) {
  return request({
    url: '/api/evaluate/export/excel/undone/student',
    responseType: "blob",
    headers: {
      "Content-Type": "application/json",
      token: Cookie.get("token"),
    },
    method: 'post',
    data: data
  })
}