import { request } from './baseURL' // 
import Cookie from 'js-cookie'
//
export function getChineseComment() {
    return request({
        url: '/api/system/list/china',
        headers: {
            'Content-Type': 'application/json',
            token: Cookie.get('token')
        },
        method: 'get'
    })
}
export function getRussianComment() {
    return request({
        url: '/api/system/list/russia',
        headers: {
            'Content-Type': 'application/json',
            token: Cookie.get('token')
        },
        method: 'get'
    })
}
//
export function editComment(data) {
    return request({
        url: '/api/system/update',
        headers: {
            'Content-Type': 'application/json',
            token: Cookie.get('token')
        },
        method: 'post',
        data: data
    })
}
//
export function addComment(data) {
    return request({
        url: '/api/system/add',
        headers: {
            'Content-Type': 'application/json',
            token: Cookie.get('token')
        },
        method: 'post',
        data: data
    })
}
//
export function deleteComment(data) {
    return request({
        url: '/api/system/delete',
        headers: {
            'Content-Type': 'application/json',
            token: Cookie.get('token')
        },
        method: 'post',
        data: data
    })
}

