//
export function getExport(data) {
  return request({
    url: '/api/score/excel/export',
    responseType: "blob",
    headers: {
      Accept: "application/json; charset=utf-8",
      'Content-Type': 'application/json',
      token: Cookie.get('token')
    },
    method: 'post',
    data: data
  })
}