<template>
  <div>
    <input type="text" v-model="input" placeholder="" />
    <el-button
      class="search"
      slot="append"
      icon="el-icon-search"
      @click="search(input)"
    ></el-button>
    <!--  -->
    <div class="addButton">
      <button @click="handleAdd"></button>
      <button @click="handleExcel">Excel</button>
    </div>
    <!--  -->
    <el-dialog
      class="addDialog"
      title=""
      :visible.sync="dialogVisible1"
      width="55%"
      :before-close="handleClose"
    >
      <!--  -->
      <el-form
        ref="form"
        :rules="rules"
        :model="form"
        :inline="true"
        label-width="100px"
      >
        <el-form-item label="" prop="cname">
          <el-input
            placeholder=""
            v-model="form.cname"
            :disabled="handleable"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="ename">
          <el-input
            placeholder=""
            v-model="form.ename"
            :disabled="handleable"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="major">
          <el-select
            v-model="form.major"
            placeholder=""
            :disabled="handleable"
          >
            <el-option label="" :value="0"></el-option>
            <el-option label="" :value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="tid">
          <el-select
            multiple
            v-model="form.tid"
            placeholder=""
            size="small"
            @change="changeTeacher"
            clearable
            collapse-tags
          >
            <el-option
              v-for="Item in teacherOptions"
              :key="Item.id"
              :label="Item.name"
              :value="Item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="grade">
          <el-select
            v-model="form.grade"
            placeholder=""
            :disabled="handleable"
          >
            <el-option :value="1" label=""></el-option>
            <el-option :value="2" label=""></el-option>
            <el-option :value="3" label=""></el-option>
            <el-option :value="4" label=""></el-option>
            <el-option :value="5" label=""></el-option>
            <el-option :value="6" label=""></el-option>
            <el-option :value="7" label=""></el-option>
            <el-option :value="8" label=""></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button @click="submit">  </el-button>
      </span>
    </el-dialog>
    <!-- excel -->
    <el-dialog
      class="addDiaglog"
      title="Excel"
      :visible.sync="dialogVisible2"
      width="40%"
    >
      <el-upload
        class="upload-demo"
        action="#"
        :before-upload="beforeUpload"
        accept=".xls, .xlsx"
        :before-remove="beforeRemove"
        :multiple="false"
        :limit="1"
        :on-exceed="handleExceed"
        :file-list="fileList"
      >
        <el-button size="small" type="primary"></el-button>
        <div slot="tip" class="el-upload__tip">
          xls/xlsx500kb
          <a @click="downloadFile"></a>
        </div>
      </el-upload>
    </el-dialog>
    <!--  -->
    <div class="common-table">
      <el-table
        height="90%"
        :data="pageTicket"
        style="width: 100%"
        stripe
        empty-text=""
      >
        <el-table-column prop="index" label="#" min-width="7%">
          <template slot-scope="scope">
            <span>{{ scope.$index + 1 }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="cname"
          label=""
          min-width="17%"
        ></el-table-column>
        <el-table-column prop="ename" label="" min-width="22%">
        </el-table-column>
        <el-table-column prop="major" label="" min-width="17%">
          <template slot-scope="scope">
            <span>{{
              scope.row.major == 1 ? "" : ""
            }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="teacher" label="" min-width="17%">
        </el-table-column>
        <el-table-column
          prop="grade"
          label=""
          min-width="17%"
        ></el-table-column>
        <el-table-column label="" min-width="20%">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.row)"
              >
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.row)"
              >
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--  -->
    <div class="footer">
      <div class="block">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentpage"
          :page-size="pagesize"
          layout="total, prev, pager, next, jumper"
          :total="tableData.length"
        >
        </el-pagination>
      </div>
      <button class="reset" @click="reset"></button>
    </div>
  </div>
</template>

<script>
import {
  downLoad,
  addCourse,
  editCourse,
  deleteCourse,
  uploadExcel,
  getCourseMessage,
  getChineseTeacherMessage,
  getRussianTeacherMessage,
} from "../utils/classApi";
export default {
  name: "ClassManage",
  data() {
    return {
      input: "",
      //token
      dialogVisible1: false, //
      dialogVisible2: false, //excel
      fileList: [],
      form: {
        cname: "",
        ename: "",
        major: "",
        tid: [],
        grade: "",
      },
      modalType: 0, // 01
      rules: {
        cname: [{ required: true, message: "" }],
        ename: [{ required: true, message: "" }],
        major: [{ required: true, message: "" }],
        tid: [{ required: true, message: "", trigger: "change" }],
        grade: [{ required: true, message: "" }],
      },
      tableData: [],
      currentpage: 1, //
      pagesize: 10, // 10
      pageTicket: [], //
      teacherOptions: [], //id
      handleable: false, //
    };
  },
  methods: {
    //
    search(input) {
      this.pageTicket = this.tableData.filter((data) => {
        return data.cname === input || data.ename === input;
      });
    },
    handleExcel() {
      this.dialogVisible2 = true;
    },
    beforeUpload: function (file) {
      var fd = new window.FormData();
      fd.append("file", file);
      uploadExcel(fd).then((res) => {
        if (res.data.code == 0) {
          this.$message.success("");
        } else {
          this.$message.error("");
          console.log(res.data);
        }
      });
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        ` 1  ${files.length}  ${
          files.length + fileList.length
        } `
      );
    },
    beforeRemove(file, fileList) {
      return this.$confirm(` ${file.name}`);
    },
    //
    submit() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          if (this.modalType === 0) {
            addCourse(this.form)
              .then((res) => {
                if (res.data.code == 0) {
                  this.$message.success("");
                  this.getList();
                } else {
                  this.$message.error("");
                  console.log(res.data);
                }
              })
              .catch((err) => {
                console.log(err);
              });
          } else {
            editCourse(this.form)
              .then((res) => {
                console.log(res);
                if (res.data.code === 0) {
                  this.$message.success("");
                  this.getList();
                } else {
                  this.$message.error("");
                  console.log(res.data);
                }
              })
              .catch((err) => {
                console.log(err);
              });
          }
          console.log(this.form, "1111");
          this.dialogVisible1 = false;
          this.$refs.form.resetFields();
        }
      });
    },
    //
    cancel() {
      this.handleClose();
    },
    //
    changeTeacher(val) {
      this.form.tid = val;
    },
    //
    handleAdd() {
      this.modalType = 0;
      this.handleable = false;
      this.dialogVisible1 = true;
    },
    //
    downloadFile() {
      downLoad().then((res) => {
        console.log(res.data);
        const blob = new Blob([res.data], {
          type: "application/vnd.ms-excel",
        });
        const link = document.createElement("a"); // a
        link.href = window.URL.createObjectURL(blob); // href
        link.download = ""; // dowload
        link.click(); // click()
      });
    },
    //
    handleClose() {
      this.dialogVisible1 = false;
      this.$refs.form.resetFields();
      // console.log(this.form);
    },
    //
    handleDelete(row) {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then(() => {
        deleteCourse({ id: row.id })
          .then((res) => {
            if (res.data.code === 0) {
              this.$message.success("");
              this.tableData = this.tableData.filter((item) => {
                return item.id !== row.id;
              });
              this.getPageInfo();
            } else {
              this.$message.error("");
            }
          })
          .catch((err) => {
            console.log(err);
          });
      });
    },
    //
    handleEdit(row) {
      this.modalType = 1;
      this.handleable = true;
      this.dialogVisible1 = true;
      this.$nextTick(() => {
        this.form = JSON.parse(JSON.stringify(row));
        (this.form.tid = []), this.form.tid.push(row.tid);
        if (this.form.grade === "") {
          this.form.grade = 1;
        } else if (this.form.grade === "") {
          this.form.grade = 2;
        } else if (this.form.grade === "") {
          this.form.grade = 3;
        } else if (this.form.grade === "") {
          this.form.grade = 4;
        } else if (this.form.grade === "") {
          this.form.grade = 5;
        } else if (this.form.grade === "") {
          this.form.grade = 6;
        } else if (this.form.grade === "") {
          this.form.grade = 7;
        } else if (this.form.grade === "") {
          this.form.grade = 8;
        }
        this.form = JSON.parse(JSON.stringify(this.form));
      });
    },
    //
    getList() {
      getCourseMessage()
        .then((res) => {
          if (res.data.code === 0) {
            this.tableData = res.data.data;
            this.getPageInfo();
          } else {
            this.$message.error("");
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getPageInfo() {
      //pageTicket
      this.pageTicket = [];
      // 
      for (
        let i = (this.currentpage - 1) * this.pagesize;
        i < this.tableData.length;
        i++
      ) {
        //pageTicket
        this.pageTicket.push(this.tableData[i]);
        //
        if (this.pageTicket.length === this.pagesize) break;
      }
    },
    handleSizeChange(size) {
      //
      this.pagesize = size;
      //
      this.getPageInfo();
    },
    handleCurrentChange(pageNumber) {
      //
      this.currentpage = pageNumber;
      //
      this.getPageInfo();
    },
    //
    reset() {
      this.getList();
      this.input = "";
    },
  },
  mounted() {
    this.getList();
    getChineseTeacherMessage()
      .then((res) => {
        this.teacherOptions = res.data.data;
      })
      .catch((err) => {
        console.log(err);
      });
    getRussianTeacherMessage()
      .then((res) => {
        this.teacherOptions = this.teacherOptions.concat(res.data.data);
      })
      .catch((err) => {
        console.log(err);
      });
    window.addEventListener(
      "popstate",
      function (e) {
        window.location.href = "/table#/table";
      },
      false
    );
  },
};
</script>

<style scoped>
/*  */
