  position: absolute;
  width: 606.93px;
  height: 37.5px;
  left: 308.15px;
  top: 40.62px;
  box-sizing: border-box;
  background: rgb(247, 247, 247);
  border: 1.25px solid rgb(127, 127, 127);
  border-radius: 6.25px;
  outline-color: transparent;
  color: rgb(127, 127, 127);
  font-family: Noto Sans SC;
  padding-left: 34.38px;
  line-height: 37.5px;
  font-size: 12px;
}
.search {
  position: absolute;
  background: transparent;
  border: none;
  left: 870.46px;
  top: 40.62px;
  height: 37.5px;
  font-size: 12px;
  border-top-right-radius: 6.25px;
  border-bottom-right-radius: 6.25px;
  color: rgb(127, 127, 127);
}
.search:active {
  color: rgb(127, 127, 127);
  border: none;
  outline: 0;
}
.search:focus,
.search:hover {
  color: rgb(127, 127, 127);
  border: none;
  background-color: transparent;
}
.addButton {
  display: flex;
  position: absolute;
  top: 40.62px;
  left: 951.97px;
  right: 154.39px;
}
.addButton button {
  height: 37.5px;
  margin-right: 25px;
  flex: 0 0 93.76px;
  color: rgb(255, 255, 255);
  font-family: Noto Sans SC;
  font-size: 16px;
  font-weight: 700;
  line-height: 28px;
  letter-spacing: 0.6250596642494202px;
  text-align: center;
  background: rgb(42, 96, 189);
  border: 1.25px solid rgb(127, 127, 127);
  border-radius: 6.25px;
}
/*  */
.el-select,
.el-input {
  width: 208px !important;
}
.dialog-footer .el-button {
  background-color: rgb(42, 96, 189);
  color: rgb(255, 255, 255);
}
a {
  float: right;
  border-bottom: 1px dotted red;
  color: rgb(42, 96, 189);
}
/*  */
.common-table {
  position: relative;
  height: 459px;
  top: 48px;
}
.el-table {
  font-size: 12px;
}
.el-table >>> .el-table__header {
  height: 37.5px !important;
}
.el-table >>> .el-table__cell {
  padding: 0;
}
.el-table >>> .el-table__row {
  height: 37.5px !important;
}
.reset {
  position: absolute;
  width: 60px;
  height: 27px;
  bottom: 35px;
  right: 38px;
  color: rgb(255, 255, 255);
  font-family: Noto Sans SC;
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
  letter-spacing: 0.6250596642494202px;
  text-align: center;
  background: rgb(42, 96, 189);
  border: 1.25px solid rgb(127, 127, 127);
  border-radius: 6.25px;
}
/*  */
.block {
  margin-top: 18px;
}
.el-pagination {
  text-align: center;
  width: 24rpx;
  height: 24rpx;
  position: absolute;
  left: 22rpx;
  border-left: 4rpx solid #ffffff;
  border-bottom: 4rpx solid #ffffff;
}
.el-pagination::before {
  content: "";
  position: absolute;
  top: -20rpx;
  left: -20rpx;
  bottom: -20rpx;
  right: -20rpx;
}
</style>